html,
body {
  background-color: #ffeded;
  margin: 0;
  height: 100%;
  width: 100%;
  font-size: 14px;
  font-family: "Helvetica Neue", Helvetica, Arial, Sans-serif, serif;
  font-weight: 300;
  text-align: center;
  cursor: default;
  transition: background-color 0.5s ease-in, box-shadow 0.5s ease-in;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -o-user-select: none;
  user-select: none;
  -ms-touch-action: none;
}

div {
  cursor: inherit;
}

.center {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;

  .dot {
    position: absolute;
    justify-content: center;
    width:  512px;
    height: 512px;
  }
  .image-icon {
    position: absolute;
    display: flex;
    top: 3%;
    right: 5%;
  }
}

